.Home .lander {
  padding: 80px 0;
  text-align: center;
}

.Home .lander h1 {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
}

.Home .lander p {
  color: #999;
}

.Home .lander div {
  padding-top: 20px;
}
.Home .lander div a:first-child {
  margin-right: 20px;
}

.Home .notes h4 {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  overflow: hidden;
  line-height: 1.5;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.Home .notes p {
  color: #666;
}
 

.titleHome {
  display: inline-block;
  margin-right: auto;
  margin-bottom: 19px;
  margin-left: auto;
  padding: 21px; 
  color: #fff;
  font-weight: 700;
  letter-spacing: 8px;
  text-shadow: none;
  width: 100%;
}

.bodyHome {
  margin: 10px;
  color:white;
}